input,
select,
textarea {
  font: 12px/1.5 arial, simsun;
}
input,
select,
textarea {
  font-size: 100%;
}
/* 让链接在 hover 状态下显示下划线 */
a:hover {
  text-decoration: underline;
}
/* 默认不显示下划线，保持页面简洁 */
ins,
a {
  text-decoration: none;
}
